// 首页，发布，按钮
<template>
    <div class="goHome">
        <template v-if="lang == 'zh'">
            <img src="../../assets/home.png" @click="goHome" v-if="$route.path !== '/home'">
            <img src="../../assets/user.png" @click="goUser" v-else>
            <img src="../../assets/publish.png" @click="goPublish">
        </template>
        <template v-else>
            <img src="../../assets/home-en.png" @click="goHome" v-if="$route.path !== '/home'">
            <img src="../../assets/user-en.png" @click="goUser" v-else>
            <img src="../../assets/publish-en.png" @click="goPublish">
        </template>
    </div>
</template>

<script>
import config from '../../config';
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
export default {
    data () {
        return {
            lang: config.lang
        };
    },
    methods: {
        // 去首页
        goHome () {
            this.$router.push({
                path: '/home'
            });
        },
        // 去个人中心
        goUser () {
            console.log(config.userList, config.userList.length, 'goUser');
            if (!config.userList.length) {
                window.toongine.protocol.request({
                    params: {
                        scheme: 'toon',
                        domain: 'ciftis',
                        path: '/getTemailList',
                        query: {}
                    },
                    callback: function (result) {
                        console.warn(result);
                    }
                });
            } else {
                this.$router.push({
                    path: '/personal'
                });
            }
        },
        // 去发布
        goPublish () {
            // 判断是否有用户
            console.log(config.userList, config.userList.length, 'goPublish');
            if (config.userList.length) {
                publicPost(getApiUrl('getTrustedUserId'), {userList: config.userList}).then((res) => {
                    let list = res;
                    // TODO: 判断可信用户是否大于1，大于1选择用户后在跳转
                    if (list.length > 1) {
                        this.$router.push({
                            path: '/edit-account',
                            query: {
                                isDependable: 1
                            }

                        });
                    // TODO: 等于1直接跳转发布
                    } else if (list.length === 1) {
                        this.$router.push({
                            path: '/publish',
                            query: {
                                userId: list[0]
                            }
                        });
                    // TODO: 没有可信用户跳转引导页
                    } else {
                        this.$router.push({
                            path: '/approve'
                        });
                    }
                }).catch(() => {
                    console.warn('error');
                });
            } else {
                window.toongine.protocol.request({
                    params: {
                        scheme: 'toon',
                        domain: 'ciftis',
                        path: '/getTemailList',
                        query: {}
                    },
                    callback: function (result) {
                        console.warn(result);
                    }
                });
                // this.$router.push({path: '/approve'});
            }
        }
    }
};
</script>

<style scoped lang='scss'>
    @import "../../scss/variables.scss";

    .goHome {
        width: pxtorem(60px);
        position: fixed;
        bottom: pxtorem(24px);
        right: pxtorem(10px);
        img {
            display: block;
            width: pxtorem(60px);
            height: pxtorem(60px);
        }
    }
</style>
